	module.exports = {"data":{"row1":{"childMarkdownRemark":{"html":"<h2>Vi hjelper deg med IT</h2>\n<p>Hallingdata hjelper næringslivet i Hallingdal og resten av landet med å ta i bruk IT på en god måte.</p>\n<p>Den teknologiske utviklingen går raskt. Markeds- og konkurransesituasjoner er i stadig endring. Myndighetenes rammebetingelser endrer seg. Alt dette stiller krav til at teknologiens muligheter utnyttes, og der er IT et nødvendig hjelpemiddel for de fleste organisasjoner.</p>\n<p>Vi kan hjelpe til med å utvikle gode IT-løsninger tilpasset din situasjon: Vi kan lage helt nye systemer, eller vi kan forbedre noe du allerede har. Vi kan få orden på dataflyten din, vi kan gi deg bedre datakvalitet, vi kan lage gode rapporter, vi kan forbedre datasikkerheten din, og vi kan hjelpe deg med din IT-strategi. Eller du kan bruke oss som en ekstern diskusjonspartner for å få gode råd på veien.</p>","timeToRead":1}}},"pathContext":{}}

